









import { Vue, Component, Watch } from 'vue-property-decorator';
import { Itasks } from './store';
import { Persist } from 'vue-local-storage-decorator';
@Component({
  components: {
    Dialog: () => import('./components/Dialog.vue'),
    List: () => import('./components/List.vue'),
  },
})
export default class App extends Vue {
  public tasks: Itasks = [];
  public appDialog: number = 0;
  public key: number = 0;
  public saveedTasks: Itasks = [];
  public async mounted() {
    if (this.saveedTasks.length) {
      this.tasks = await this.$store.dispatch(
        'asyncChangeTasks',
        this.saveedTasks,
      );
    } else {
      this.tasks = [];
      this.saveedTasks = this.tasks;
    }
  }
  @Persist()
  public async saveTasks() {
    this.persistData('saveedTasks');
    this.tasks = await this.$store.dispatch('asyncChangeTasks', this.saveedTasks);
  }
  // 监控数据变化，包括完成度,触发保存到localstroage
  @Watch('tasks', { deep: true })
  public onTasksChanged(val: Itasks, oldVla: Itasks) {
    this.saveTasks();
  }
  public changeDialog(key: number): void {
    this.key = key;
    this.appDialog++;
  }
}
