import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export interface Itask {
  id: number;
  value: string;
  tasked: boolean;
}

export interface Itasks {
  [index: number]: Itask;
  length?: number;
  push?: any;
  splice?: any;
}
export interface Istate {
  tasks: Itasks;
}
const state: Istate = {
  tasks: [
    {
      id: 0,
      value: '第一个任务',
      tasked: false,
    },
    {
      id: 1,
      value: '第二个任务',
      tasked: false,
    },
    {
      id: 2,
      value: '第三个任务',
      tasked: false,
    },
  ],
};
type icommit = (str: string, obj: any) => void;
interface Icontext {
  commit: icommit;
}

type ichageTasks = (states: Istate, newtasks: Itasks) => void;

const changeTasks: ichageTasks = (states: Istate, newtasks: Itasks) => {
  states.tasks = newtasks;
};

function addTask(states: Istate, newtask: Itask) {
  states.tasks.push(newtask);
}
function delTask(states: Istate, key: number) {
  states.tasks.splice(key, 1);
}

function asyncChangeTasks(context: Icontext, newtasks: Itasks): Itasks {
  context.commit('changeTasks', newtasks);
  return newtasks;
}

function asyncAddtask(context: Icontext, newtask: Itask): Itask {
  context.commit('addTask', newtask);
  return newtask;
}
function asyncDelTasks(context: Icontext, key: number): number {
  context.commit('delTask', key);
  return key;
}
export default new Vuex.Store({
  state,
  mutations: {
    changeTasks,
    addTask,
    delTask,
  },
  actions: {
    asyncChangeTasks,
    asyncAddtask,
    asyncDelTasks,
  },
});
